body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dashboardWrapper {
  min-height:55vh;
}

.dashboard {
  display: inline-block;
  padding: 15px 25px;
  font-size: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: #4CAF50;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
  width:31%;
}
.dashboard:hover{background-color: #3e8e41}
.dashboard:active{
    box-shadow:0 5px, #666;
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
}
@media only screen and (max-width: 750px) {
    .dashboard{
        width:100%;
    }
}
* { 
  font-style:Sans-serif;
  font-weight:bold;
}
.App {
  text-align: center;
  min-height:60vh;
  padding-top:5rem;
}
#root {
  width: 100%;
  text-align: center;
  
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color:#8ac43f;
  min-height: 28vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.head-title{
  color:#8ac43f;
}
button {
  display: inline;
  margin: 2px;
}
.button-container {
  display: inline;
}




/* carasoul and pagination style */

.middle {
  position: relative;
  margin: 0 30px;
}
.carousel-wrapper {
  position: relative;
}
.picture {
  display: inline-block;
  position:relative;
  height:60vh;
  width: 100%;
  transition: all 0.5s ease-in-out;
}

.image-container {
  display: inline-block;
  width: 100%;
  box-shadow: 10px 10px 5px grey;
  overflow: hidden;
  white-space: nowrap;
  transition: all 0.5s ease-in-out;
}
.next-button {
  width: 100%;
  z-index: 999;
  height: 60px;
  border: none;
  background: url('/images/next-arrow.png') no-repeat top right;
  padding: 2px 8px;
  cursor: pointer;
  outline:none;
}
.next-button:hover{
  width: 100%;
  height: 70px;
  border: none;
  outline: none;
  background: url('/images/next-arrow-hover.png') no-repeat top right;
  padding: 2px 8px;
  cursor: pointer;
}
.next-button:active{
 outline: none;
 border:none
}
.next-button:visited{
 outline: none;
 border:none
}

.previous-button {
  width: 100%;
  height: 60px;
  z-index: 999;
  border: none;
  outline: none;
  background: url('/images/previous-arrow.png') no-repeat top left;
  padding: 2px 8px;
  cursor: pointer;
 
}
.previous-button:hover{
  width: 100%;
  height: 70px;
  border: none;
  outline:none;
  background: url('/images/previous-arrow-hover.png') no-repeat top left;
  padding: 2px 8px;
  cursor: pointer;
}
.previous-button:active{
 outline: none;
 border: none;
}
.previous-button:visited{
 outline: none;
 border:none
}
.next-container {
  display: inline-block;
  width: 15%;
  position: absolute;
  top: 100px;
  right: 0;
}
.previous-container {
  display: inline-block;
  width: 15%;
  position: absolute;
  top: 100px;
  left: 0;
}

.active-class {
  background-color: green !important;
}
.completed-class {
  background-color: orangered;
}

.dashboard-btn {
  max-width: 400px;
  width: 100%;
  margin: 20px 0;
  background-color: d2d4d6
}

.caption {
    width: 500px;
    height: 80px;
