/* login form style  */

.formWrapper {
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
.errorMessage {
  color: red;
}
ul {
  list-style: none;
}

input {
  padding: 0.5rem;
  font-size: 16px;
  width: 100%;
  display: block;
  border-radius: 4px;
  border: 1px solid #ccc;
}

input:focus {
  border-color: #007eff;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
  outline: none;
}

input.error {
  border-color: red;
}

label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.input-feedback {
  color: red;
  margin-top: 0.25rem;
}



button:disabled {
  opacity: 0.5;
  cursor: not-allowed !important;
}

/* button + button {
  margin-left: 0.5rem;
} */

button.secondary {
  padding: 0.5rem;
  width:100%;
  background-color: #eee;
  border: 1px solid #aaa;
  color: #555;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.bg-color-header {
  background-color: #8ac43f;
  text-align: center;
  color: white;
  height:20vh;
}
#logo{
    width:200px;
    width:200px;
    float:left;
}
.bg-color-footer {
  background-color: #8ac43f;
  text-align: center;
  color: black;
  height:20vh;
  padding: 70px 0;
  margin: 10px 0 0 0;
 
}


